import React from 'react'
import styles from '../Cards.module.css'
import ArrowRightIcon from '@mui/icons-material/ArrowRight';

function EstimateReqCard({ estimate, apply }) {

  return (
    <div>
      {estimate && <EstimateCard estimate={estimate} />}
      {apply && <ApplyCard apply={apply} />}
    </div>
  )
}

export default EstimateReqCard

const EstimateCard = ({ estimate }) => {

  const created = estimate.created.split('T')[0]

  return (
    <div className={styles.card}>
      <div className={styles.imgContainer}>
        <label className={styles.statusLabel}>
          {
            estimate.status === 1 &&
            <div>
              <span> </span>
              <span></span>
            </div>
          }
          {
            estimate.status === 2 &&
            <div>
              <span> </span>
              <span></span>
            </div>
          }
          {estimate.status === 3 && ""}
          {(estimate.status === 4 && estimate.status === 6) && ""}
        </label>
      </div>
      <div className={styles.cardInfoContainer}>
        <span>  : {estimate.estimateIdx}</span>
        <span>created : {created}</span>
      </div>
      <div className={styles.cardInfoBtn}>
        <ArrowRightIcon fontSize='large' className={styles.btn} />
      </div>
    </div>
  )
}

const ApplyCard = ({ apply }) => {
  return (
    <div className={styles.card}>
      <div className={styles.imgContainer}>
        <label className={styles.statusLabel}>
          {((apply.status === 1 || apply.status === 2) && !apply.isApplied) && " "}
          {((apply.status === 1 || apply.status === 2) && apply.isApplied) && " "}
          {apply.status === 3 && ""}
          {apply.status === 4 && ""}
          {apply.status === 5 && ""}
          {apply.status === 6 && ""}
          {apply.status === 7 && ""}
          {apply.status === 8 && ""}
        </label>
      </div>
      <div className={styles.cardInfoContainer}>
        <span>  : {apply.estimateIdx}</span>
        {/* <span>created : </span> */}
      </div>
      <div className={styles.cardInfoBtn}>
        <ArrowRightIcon fontSize='large' className={styles.btn} />
      </div>
    </div>
  )
}